import {call,takeLatest,put} from 'redux-saga/effects';
import axios from 'axios';
import {
  FETCH_NEWS
} from '../actions/types';
import {
  fetchNewsSuccess,
  fetchImagesSuccess,
  setError
} from '../actions';

export function* fetchNewsSaga() {
  try {
    yield takeLatest(FETCH_NEWS, function* ({payload}) {
      const {data} = yield call(axios.get,`/api/images/offset/${payload}`);
      const newsIds = data.images.map(({_id}) => _id);
      data.images.reverse();
      yield put(fetchImagesSuccess(data.images));
      yield put(fetchNewsSuccess(newsIds,data.size));
    });
  }
  catch(error) {
    yield put(setError());
  }
}


