import {call,select,put,take} from 'redux-saga/effects';
import axios from 'axios';
import {fetchImages} from '../images/fetchImages';
import {getAlbumById} from '../../selectors/index';
import {
  GET_ALBUM
} from '../../actions/types';
import {
  getAlbumSuccess,
  setError
} from '../../actions/index';

export function* getAlbumSaga() {
  try {
    const {payload: {id}} = yield take(GET_ALBUM);
    let album = yield select(getAlbumById,id);
    if(album) {
      yield put(getAlbumSuccess(album));
      album = album.toJS();
    }
    else {
      const { data } = yield call(axios.get,`/api/albums/${id}`);
      album = data;
      yield put(getAlbumSuccess(album));
    }
    yield call(fetchImages,album._id);
  }
  catch(error) {
    console.log(error);
    yield put(setError());
  }
}


