import {call,put} from 'redux-saga/effects';
import axios from 'axios';
import {
  fetchImagesSuccess
} from '../../actions/index';

export function* fetchImages(id) {
  const {data} = yield call(axios.get,`/api/images/${id}`);
  yield put(fetchImagesSuccess(data));
}


